___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --font-hierarchy: Helvetica, Arial, sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol';

  --backGroundColour: #74c3d1;
  --borderColour: #000000;
  --lightTextColour: #f8f1e4;

  --lightestColour: rgb(240, 240, 255);
  --incompleteTask: rgb(210, 182, 214);
  --completeTask: rgb(233, 220, 235);
  --midDarkColour: rgb(90, 197, 160);
  --darkestColour: #003049;

  --lowPrio: rgb(17, 227, 17);
  --midPrio: rgb(236, 192, 15);
  --highPrio: rgb(219, 14, 14);

  /* Main container width max of 80% vw and fixed width, centred */
  --minContentWidth: 1200px;
  --bodyWidth: min(var(--minContentWidth), 90vw);
  --bodyWidthMargin: max(calc(0.5 * (100vw - var(--minContentWidth))), 5vw);

  /* Main container height of 90% vh, centred */
  --bodyHeight: 90vh;
  --bodyHeightMargin: calc(0.5 * (100vh - var(--bodyHeight)));

  --weatherDaySquareBoxWidth: 120px;

  --header-scaler: 4rem;
}

html {
  /* set the rem */
  font-size: 62.5%;
}

body {
  background-color: var(--backGroundColour);
  font-family: var(--font-hierarchy);
  font-size: 2rem;
}

p {
  margin: 0;
}

#main-container {
  background-color: var(--lightestColour);
  width: var(--bodyWidth);
  height: var(--bodyHeight);
  margin: var(--bodyHeightMargin) var(--bodyWidthMargin);
  display: grid;
  grid-template-columns: 350px 1fr;
  column-gap: 20px;
}

#sidebar-container {
  padding: 40px 25px;
  background-color: var(--midDarkColour);
  box-shadow: 0px 6px 20px var(--midDarkColour);
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: repeat(2, auto);
  align-content: start;
  row-gap: 80px;
}

#page-title-container {
  display: grid;
  column-gap: 20px;
  align-items: center;
  grid-template-columns: auto 1fr;
}

#page-title-icon {
  width: 40px;
}

#page-title-text {
  font-size: 2.5rem;
  font-weight: 800;
}

/* used to avoid input styling adjusting when autocomplete list is visible */
#location-top-container {
  position: relative; /* autocomplete list relative to this */
}

#location-input-container {
  background-color: var(--lightestColour);
  border: 2px solid black;
  display: grid;
  grid-template-columns: 1fr auto;
  column-gap: 20px;
  align-items: center;
  font-size: 2rem;
}

#location-input-text {
  background-color: var(--lightestColour);
  border: none;
  padding: 0 10px;
  /* font-size: 20px; */
}

input:focus {
  outline: none;
}

#location-input-icon {
  background-color: var(--lightestColour);
  width: 40px;
}

#location-autocomplete {
}

.autocomplete-invis {
  visibility: hidden;
}

.autocomplete-vis {
  position: absolute;
  top: 50px;
  visibility: visible;
}

#display-container {
  padding: 50px 30px;
  display: grid;
  justify-content: center;
  align-content: start;
  grid-template-rows: repeat (2, auto);
  row-gap: 80px;
}

#location-display {
  display: grid;
  place-items: center;
  font-size: 3rem;
}

#daily-weather-display {
  padding: 30px;
  display: grid;
  justify-content: stretch;
  grid-template-rows: repeat(2, auto);
  border: 2px solid black;
}

#today-weather-display {
  padding: 20px;
  grid-row: 1;
  grid-column: 1 / 4;
  border: 2px solid green;
  display: grid;
  place-items: center;
}

.forecast-day-display {
  grid-row: 2;
  width: var(--weatherDaySquareBoxWidth);
  height: var(--weatherDaySquareBoxWidth);
  border: 2px solid green;
  display: grid;
  place-items: center;
}
